import { Item } from "../shared/Item.model";
import { CategoryType } from "./Category-type.component";
import { State } from "./State.component";
// import { string } from "../string.component";

export class Category{
  public _CategoryId: string;
  public _Name:string;
  public _Description: string;
  public _Price:number;
  public _ImagePath:string;
  public itemBought: Item[];
  public _Size:string;
  public _CategoryType:CategoryType;
  public _Number:number;
  public state:State;

  /**
   *
   */
  constructor(_Name:string,desc:string,_Price:number,imgPth:string,_Size:string,Number1:number,_CategoryId:string,_CategoryType:CategoryType) {
    this._Name = _Name;
    this._Description = desc;
    this._Price = _Price;
    this._ImagePath = imgPth;

    this._Size = _Size;
    this._CategoryType = _CategoryType;
    this._CategoryId = _CategoryId;
    this._Number = Number1;
  }

}
